// src/components/Categories.tsx

"use client";
import { useLayoutEffect, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import CompetitionCard from './CompetitionCard';

gsap.registerPlugin(ScrollTrigger);

// තරඟ දත්ත. අවශ්‍ය පරිදි මෙතනට තවත් තරඟ එක් කරන්න.
const competitions = [
    { title: "CODINEX!!", subtitle: "Inter-School Coding Competition", logoSrc: "/logos/codinex.png" },
    { title: "pixelynx", subtitle: "Digital Art & Design Competition", logoSrc: "/logos/pixelynx.png" },
    { title: "MECHATRON", subtitle: "Robotics & IoT Challenge", logoSrc: "/logos/mechatron.png" },
    { title: "VIRTUEVERSE", subtitle: "Inter-School E-Sports Arena", logoSrc: "/logos/virtueverse.png" },
    { title: "CYBER UEST", subtitle: "Cyber Security & Ethical Hacking", logoSrc: "/logos/cyberquest.png" },
    { title: "CIPHER", subtitle: "Inter-School Tech Quiz Challenge", logoSrc: "/logos/cipher.png" },
    { title: "QUESTRIX", subtitle: "Inter-School Puzzle Challenge", logoSrc: "/logos/questrix.png" },
    { title: "Most Popular", subtitle: "Most Popular School ICT Society", logoSrc: "/logos/award.png" },
];

const Categories = () => {
    const sectionRef = useRef(null);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            gsap.from(".category-card", {
                scrollTrigger: {
                    trigger: sectionRef.current,
                    start: "top 80%",
                },
                opacity: 0,
                y: 100,
                duration: 0.8,
                stagger: 0.1,
                ease: 'power3.out'
            });
        }, sectionRef);
        return () => ctx.revert();
    }, []);

    return (
        <section id="categories" ref={sectionRef} className="w-full bg-black py-24">
            <div className="container mx-auto px-6 md:px-10">
                <h2 className="text-4xl md:text-5xl font-bold uppercase tracking-wider text-center text-purple-400 mb-16">
                    Categories
                </h2>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
                    {competitions.map((comp, index) => (
                        <div key={index} className="category-card">
                            <CompetitionCard 
                                title={comp.title}
                                subtitle={comp.subtitle}
                                logoSrc={comp.logoSrc}
                            />
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Categories;